import { BuildContext, Component, HtmlElement, StatelessComponent } from "@equantic/runtime";
import { Column } from "./Column";
import { Container } from "./Container";
import { Flex } from "./Flex";
import { Text } from "./Text";

export class Alert extends StatelessComponent {
    build(context: BuildContext) {
        let [bgColor, textColor, borderColor] = (this.type === 'success' ? ['bg-green-50 dark:bg-green-900/20', 'text-green-800 dark:text-green-200', 'border-green-200 dark:border-green-800'] : (this.type === 'warning' ? ['bg-yellow-50 dark:bg-yellow-900/20', 'text-yellow-800 dark:text-yellow-200', 'border-yellow-200 dark:border-yellow-800'] : (this.type === 'error' ? ['bg-red-50 dark:bg-red-900/20', 'text-red-800 dark:text-red-200', 'border-red-200 dark:border-red-800'] : ['bg-blue-50 dark:bg-blue-900/20', 'text-blue-800 dark:text-blue-200', 'border-blue-200 dark:border-blue-800'])));let content = new Column({ gap: '4px' });if (!(!this.title || this.title === '')) {content.children.push(new Text(this.title, { className: 'font-bold' }));}if (!(!this.message || this.message === '')) {content.children.push(new Text(this.message, { className: 'text-sm opacity-90' }));}return new Container({ className: `p-4 rounded-md border ${bgColor} ${textColor} ${borderColor} ${this.className}`, children: [content] });
    }

}

