import { BuildContext, Component, ComponentState, getServerActionsClient, HtmlElement, InputComponent, StatefulComponent, StatelessComponent } from "@equantic/runtime";
import { Alert } from "./Alert";
import { Button } from "./Button";
import { Card } from "./Card";
import { Checkbox } from "./Checkbox";
import { Column } from "./Column";
import { Container } from "./Container";
import { Drawer } from "./Drawer";
import { Flex } from "./Flex";
import { Heading } from "./Heading";
import { Modal } from "./Modal";
import { Row } from "./Row";
import { Text } from "./Text";
import { TextInput } from "./TextInput";

export class TodoList extends StatefulComponent {
    createState() {
        return new TodoListState(this)
    }

    async getTodos() {
        return await getServerActionsClient().invoke('TodoList/GetTodos', [])
    }

    async addTodo(title: string) {
        return await getServerActionsClient().invoke('TodoList/AddTodo', [title])
    }

    async toggleTodo(id: string) {
        return await getServerActionsClient().invoke('TodoList/ToggleTodo', [id])
    }

    async deleteTodo(id: string) {
        return await getServerActionsClient().invoke('TodoList/DeleteTodo', [id])
    }

    async updateTodo(id: string, title: string) {
        return await getServerActionsClient().invoke('TodoList/UpdateTodo', [id, title])
    }

    async clearCompleted() {
        return await getServerActionsClient().invoke('TodoList/ClearCompleted', [])
    }

}


export class TodoListState extends ComponentState {
    _component: TodoList;
    _needsRender: boolean = false;
    _todos: Todo[] = [];
    _newTodoTitle: string = '';
    _currentFilter: TodoFilter = 'all';
    _isEditing: boolean = false;
    _editingId: string = null;
    _editingTitle: string = '';
    _isDrawerOpen: boolean = false;
    constructor(component: TodoList) {
        super();
        this._component = component;
    }

    setState(fn: () => void) {
        fn();
        this._needsRender = true;
        this._component._scheduleRender();
    }

    onInit(): void {this.loadTodos();}

    async loadTodos(): Promise<void> {this._todos = await this._component.getTodos();this.setState(() => {});}

    async handleAdd(): Promise<void> {if ((!this._newTodoTitle || this._newTodoTitle.trim() === '')) return;await this._component.addTodo(this._newTodoTitle);this._newTodoTitle = '';await this.loadTodos();}

    async handleToggle(id: string): Promise<void> {await this._component.toggleTodo(id);await this.loadTodos();}

    async handleDelete(id: string): Promise<void> {await this._component.deleteTodo(id);await this.loadTodos();}

    async handleClearCompleted(): Promise<void> {await this._component.clearCompleted();await this.loadTodos();}

    openEdit(todo: Todo): void {this.setState(() => {this._isEditing = true;this._editingId = todo.id;this._editingTitle = todo.title;});}

    async saveEdit(): Promise<void> {if ((!this._editingTitle || this._editingTitle.trim() === '')) return;await this._component.updateTodo(this._editingId, this._editingTitle);this.setState(() => {this._isEditing = false;});await this.loadTodos();}

    createFilterButton(filter: TodoFilter, label: string): Button {let isActive = this._currentFilter === filter;let activeClass = isActive ? 'bg-white dark:bg-zinc-800 text-blue-600 shadow-sm' : 'text-gray-500 hover:text-gray-700 dark:text-gray-400 dark:hover:text-gray-200';return new Button({ text: label, className: 'flex-1 px-3 py-1.5 text-xs font-semibold rounded-md transition-all ' + activeClass, onClick: () => {this.setState(() => {this._currentFilter = filter;});} });}

    build(context: BuildContext) {
        let filteredTodos = (this._currentFilter === 'active' ? this._todos.filter((t) => !t.isCompleted) : (this._currentFilter === 'completed' ? this._todos.filter((t) => t.isCompleted) : this._todos));let todoList = new Column({ gap: '5px' });if (!(filteredTodos.length > 0)) {todoList.children.push(new Container({ className: 'py-8 text-center text-gray-500 italic', children: [new Text('No tasks found in this view.')] }));} else {for (const todo of filteredTodos) {todoList.children.push(new Row({ className: 'items-center p-2 border-b border-gray-100 dark:border-zinc-700 bg-white dark:bg-zinc-800 rounded transition-all hover:bg-gray-50 dark:hover:bg-zinc-700/50 group', gap: '10px', children: [new Checkbox({ checked: todo.isCompleted, onChange: (_) => {this.handleToggle(todo.id);} }), new Text(todo.title, { className: `flex-1 ${(todo.isCompleted ? 'line-through text-gray-400' : 'text-gray-900 dark:text-gray-100')}` }), new Row({ gap: '4px', className: 'opacity-0 group-hover:opacity-100 transition-opacity', children: [new Button({ text: 'Edit', className: 'px-2 py-1 text-xs text-blue-600 hover:bg-blue-50 dark:hover:bg-blue-900/30 rounded', onClick: () => {this.openEdit(todo);} }), new Button({ text: 'Delete', className: 'px-2 py-1 text-xs text-red-600 hover:bg-red-50 dark:hover:bg-red-900/30 rounded', onClick: () => {this.handleDelete(todo.id);} })] })] }));}}return new Container({ className: 'min-h-screen bg-gray-50 dark:bg-zinc-900 p-8 flex justify-center', children: [new Container({ className: 'absolute top-4 right-4', children: [new Button({ text: '⚙️ Settings', className: 'px-3 py-2 bg-white dark:bg-zinc-800 border border-gray-200 dark:border-zinc-700 rounded-lg shadow-sm hover:shadow-md text-sm', onClick: () => {this.setState(() => {this._isDrawerOpen = true;});} })] }), new Card({ width: 'w-full max-w-md', shadow: 'large', header: new Column({ gap: '15px', children: [new Row({ className: 'items-center justify-between', children: [new Heading('Tasks', 2, { className: 'text-xl font-bold text-gray-800 dark:text-white' }), new Text(`${this._todos.filter((t) => !t.isCompleted).length} remaining`, { className: 'text-sm text-gray-500' })] }), new Row({ gap: '8px', className: 'bg-gray-100/50 dark:bg-zinc-900/30 p-1 rounded-lg', children: [this.createFilterButton('all', 'All'), this.createFilterButton('active', 'Active'), this.createFilterButton('completed', 'Completed')] })] }), body: new Column({ gap: '20px', children: [new Row({ gap: '10px', children: [new TextInput({ value: this._newTodoTitle, placeholder: 'New task...', className: 'flex-1 px-3 py-2 border rounded border-gray-300 dark:border-zinc-600 dark:bg-zinc-800 dark:text-white focus:ring-2 focus:ring-blue-500 outline-none transition-all', onInput: (val) => {this.setState(() => {this._newTodoTitle = val;});} }), new Button({ text: 'Add', className: 'px-4 py-2 bg-blue-600 text-white rounded hover:bg-blue-700 font-medium shadow-sm transition-colors', onClick: () => {this.handleAdd();} })] }), todoList] }), footer: this._todos.some((t) => t.isCompleted) ? new Row({ className: 'justify-center', children: [new Button({ text: 'Clear Completed', className: 'text-sm text-red-500 hover:text-red-700 transition-colors uppercase font-semibold tracking-wider', onClick: () => {this.handleClearCompleted();} })] }) : null }), new Modal({ isOpen: this._isEditing, title: 'Edit Task', onClose: () => {this.setState(() => {this._isEditing = false;});}, body: new Column({ gap: '10px', children: [new Text('Update the task title:', { className: 'text-sm text-gray-500 dark:text-gray-400' }), new TextInput({ value: this._editingTitle, className: 'w-full px-3 py-2 border rounded border-gray-300 dark:border-zinc-600 dark:bg-zinc-800 dark:text-white focus:ring-2 focus:ring-blue-500 outline-none', onInput: (val) => {this.setState(() => {this._editingTitle = val;});} })] }), footer: new Row({ gap: '10px', children: [new Button({ text: 'Cancel', className: 'px-4 py-2 text-gray-600 dark:text-gray-400 hover:bg-gray-100 dark:hover:bg-zinc-700 rounded transition-colors', onClick: () => {this.setState(() => {this._isEditing = false;});} }), new Button({ text: 'Save Changes', className: 'px-4 py-2 bg-blue-600 text-white rounded hover:bg-blue-700 transition-colors font-medium', onClick: () => {this.saveEdit();} })] }) }), new Drawer({ isOpen: this._isDrawerOpen, side: 'right', onClose: () => {this.setState(() => {this._isDrawerOpen = false;});}, content: new Column({ gap: '20px', children: [new Heading('Settings', 3, { className: 'text-xl font-bold mb-4 dark:text-white' }), new Text('Theme preferences and other settings will go here.', { className: 'text-gray-600 dark:text-gray-400' }), new Alert({ type: 'success', message: 'eQuantic.UI is now running with Bun + Tailwind 4!' }), new Container({ className: 'mt-auto pt-8 border-t border-gray-100 dark:border-zinc-800', children: [new Button({ text: 'Back to List', className: 'w-full py-2 bg-gray-100 dark:bg-zinc-800 text-gray-700 dark:text-gray-300 rounded hover:bg-gray-200 dark:hover:bg-zinc-700 transition-colors', onClick: () => {this.setState(() => {this._isDrawerOpen = false;});} })] })] }) })] });
    }

}


